@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;700&display=swap);
*{padding:0;margin:0}html,body{font-family:'Rubik', sans-serif;height:100vh}.wrapper{min-height:100vh;background:#EAEAEA}.container{max-width:1170px;margin:0 auto;display:flex;flex-direction:column}.header{display:flex;align-items:center}.header h1{font-weight:300}.header img{width:64px;height:64px}.main{padding:15px;display:flex;flex-direction:column}.main h3 span{font-weight:300}.main h3 .password{font-weight:700}.main h3 .copy-btn{margin-left:5px;height:30px;padding:0 10px;background:#EAEAEA;border:1px solid #353535;cursor:pointer}.main-btn{margin:50px 0;background:#00EDF1;color:#353535;justify-self:center;align-self:center;font-size:48px;padding:15px 30px;cursor:pointer;border:2px solid #353535;border-radius:30px;transition:-webkit-transform .2s ease-in-out;transition:transform .2s ease-in-out;transition:transform .2s ease-in-out, -webkit-transform .2s ease-in-out}.main-btn:hover{-webkit-transform:scale(1.03, 1.03);transform:scale(1.03, 1.03)}

